
.app {
  width: 100%;
  font-family: 'Lato', "PingFang SC", "Microsoft YaHei", sans-serif;
  font-size: 14px;
  line-height: 2;
}
.content{
  width: 90%;
  max-width: 960px;
  margin: 0 auto;
  padding-left: calc(100vw - 100%);
}
.clearfix::after{
  content: '';
  display: block;
  visibility: hidden;
  height: 0;
  clear: both;
}
.ico-menu-wrapper{
  padding: 10px;
  position: absolute;
  top: 20px;
  left: 10%;
}
.ico-menu{
  border-bottom: 2px solid #fff;
  border-top: 2px solid #fff;
  padding: 4px 0;
  background-clip: content-box;
  background-color: #fff;
  width: 22px;
  height: 2px;
}
aside{
  padding:1px 0; 
  min-height: 80vh;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity 1s;
  transition: opacity 1s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  opacity: 0
}
footer{
    width:100%;
    height: 100px;
    margin: 0 auto;
    background: url('/static/img/back3.jpg') no-repeat;
    background-size: 100% 100px;
}
@media screen and (min-width: 768px){
nav{
        width: 240px;
        float: left;
        color: #fff;
}
aside{
        margin-left: 260px;
        background: #fff;
}
.ico-menu-wrapper{
        display: none;
}
}
@media screen and (max-width: 768px) {
nav{
        width: 100%;
}
.content{
        width: 100%;
}
}



