
#home[data-v-bd1fd060]{
	font-size: 11px;
    font-family: 'Open Sans', sans-serif;
    color: #4A4A4A ;
    text-align: center;
}
.container[data-v-bd1fd060]{
 	width: 90%;
 	height: 400px;
 	margin: 20px auto;
